@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

$color-bg: #1f1f38;
$color-bg-variant: #2c2c6c;
$color-primary: #4db5ff;
$color-primary-variant: rgba(77, 181, 255, 0.4);
$color-white: #fff;
$color-light: rgba(255, 255, 255, 0.6 );
$container-widt-lg: 75%;
$container-width-md: 86%;
$container-width-sm: 90%;

$transition: all 400ms ease;
@import './components/header/scss/header.scss';
@import './components/nav/scss/nav.scss';
@import './components/about/scss/about.scss';
@import './components/experience/scss/experience.scss';
@import './components/services/scss/services.scss';
@import './components/Portfolio/scss/portflio.scss';
@import './components/testimonial/scss/testimonial.scss';
@import './components/contact/scss/contact.scss';
@import './components/footer/scss/footer.scss';
*{
    margin: 0px;
    padding: 0px;
    border: none;
    outline: none;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

::-webkit-scrollbar{
    display: none;
}

html{
    scroll-behavior: smooth;
}

body{
    font-family: 'Poppins', sans-serif;
    background: $color-bg;
    color: $color-white;
    background-image: url(./assets/backgroundPattern.png);
    background-size: 40px;
    background-repeat: repeat;
}

.container{
    width: $container-widt-lg;
    margin: 0 auto;
}

h1, h2, h3, h4, h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
}

section > h2, section > h5{
    text-align: center;
    color: $color-light;
}

section > h2 {
    color: $color-primary;
    margin-bottom: 3rem;
}

.text-light{
    color: $color-light;
}

a{
    color: $color-primary;
    transition: $transition;
    &:hover{
        color: $color-white;
        cursor: pointer;
    }
    &.also-underline{
        text-decoration: underline;
    }
}

.btn{
    width: max-content;
    display: inline-block;
    color: $color-primary;
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid $color-primary;
    transition: $transition;

    &:hover{
        background-color: $color-white;
        color: $color-bg;
        border-color: transparent ;
    }
}

.btn-primary{
    background-color: $color-primary;
    color: $color-bg;
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}


// ###### MEDIA QUERY ######
@media screen and (max-width: 1024px){
    .container{
        width: $container-width-md;        
    }
    section{
        margin-top: 6rem;
    }
}

@media screen and (max-width: 600px){
    .container{
        width: $container-width-sm;        
    }
    section > h2{
        margin-top: 2rem;
    }
}

