.container.testimonials__container{
    width: 35%;
    padding-bottom: 4rem;
}
.client__avatar{
    width: 4rem;
    aspect-ratio:  1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 1rem;
    border: 0.4rem solid $color-primary-variant;
}

.testimonial{
    background-color: $color-bg-variant;
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    user-select: none;
}

.client__review{
    color: $color-light;
    font-weight: 300;
    display: block;
    width: 80%;
    margin: 0.8rem auto 0;
}

.swiper-pagination-clickable .swiper-pagination-bullet{
    background-color: $color-primary;
}

// ###### MEDIA QUERY ######
@media screen and (max-width: 1024px){
    container.testimonials__container{
        width: 60%
    }    
}

@media screen and (max-width: 600px){
    .container.testimonials__container{
        width: $container-width-sm;        
    }
    .client__review{
        width: $container-width-sm;        
    }
}